$blue: #1EACC7;
$pink: #FF5656;

main {
  color: #070716; }

@mixin btn_hover {
  &:hover {
    background-image: none;
    background-color: $blue;
 } }    // transition: 0.5s all

@mixin foot_link {
  text-decoration: none;
  font-weight: 300;
  font-size: 15px;
  line-height: 453%;
  text-transform: uppercase;
  color: #070716; }

@mixin foot_sublink {
  text-decoration: none;
  font-weight: 300;
  font-size: 11px;
  line-height: 618%;
  text-transform: uppercase;
  color: #999; }

* {
  font-family: "Roboto", sans-serif; }
header {
  position: relative;
  height: 50px; }
nav {
  width: 100%;
  height: 50px;
  background-color: #212121; }
.menu {
  display: flex;
  justify-content: space-between;
  padding: 15px 36px 10px 71px;
  margin: 0;
  list-style-type: none;
  &_link {
    text-decoration: none;
    font-weight: 300;
    font-size: 15px;
    line-height: 100%;
    color: #fff; }

  &_item {
    border-right: 2px dotted black;
    padding-right: 28px;
    &:hover {
      background-color: grey;
      transition: 0.4s all; } } }
.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);
  .subheader_logo {
    display: block;
    margin-top: 17px;
    max-width: 100%; }
  .subheader_official {
    font-weight: 100;
    font-size: 13px;
    color: #fff;
    margin-top: 6px; }
  .subheader_call {
    margin-top: 19px;
    font-weight: 300;
    font-size: 12px;
    text-align: right;
    color: #fff; }
  .subheader_phone {
    display: block;
    text-align: right;
    margin-top: 8px;
    line-height: 12px;
    font-weight: 400;
    font-size: 22px;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff; }
  .subheader_btn {
    margin-top: 19px;
    border-radius: 4px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 0 rgba(0, 0, 0, 0.12);
    background-image: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
    border: none;
    font-weight: 400;
    font-size: 14px;
    text-transform: uppercase;
    cursor: pointer;
    text-align: center;
    text-shadow: 1px 1px 0 0 rgba(0, 0, 0, 0.21);
    color: #fff;
    @include btn_hover; } }
.promo {
  min-height: 800px;
  background: url("../image/bg_pc/uber_bg.png") center center/cover no-repeat;
  padding: 150px 0 185px 0;
  .poromo_header {
    font-weight: 900;
    font-size: 60px;
    line-height: 60px;
    text-transform: uppercase;
    text-align: center;
    margin: 0;
    text-align: center;
    color: #fff; }
  &_subheader {
    font-weight: 700;
    font-size: 36px;
    line-height: 40%;
    text-transform: uppercase;
    text-align: center;
    margin: 0;
    margin-top: 22px;
    text-align: center;
    color: #fff; }
  &_text {
    margin-top: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    color: #fff; }
  &_btn {
    display: block;
    margin: 118px auto;
    border-radius: 4px;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.12);
    background: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
    border: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 350%;
    text-transform: uppercase;
    cursor: pointer;
    text-align: center;
    text-shadow: 1px 1px 0 0 rgba(0, 0, 0, 0.21);
    color: #fff;
    @include btn_hover; } }
.reasons {
  background-color: #FFFFFF;
  padding: 18px 0 64px 0;
  min-height: 765px;
  &_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }
  &_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    min-width: 113px;
    min-height: 113px;
    background-color: $blue;
    border-radius: 100%; }
  &_descr {
    width: 372px; }
  &_subhead {
    line-height: 21px;
    color: $blue;
    font-size: 17px;
    font-weight: 400;
    line-height: 124%; }
  &_text {
    color: #202020;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px; } }

.label {
  display: block;
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center;
  &_white {
    background-color: #fff;
    color: #000; } }

.title {
  margin-top: 33px;
  font-weight: 700;
  font-size: 38px;
  text-transform: uppercase;
  text-align: center;
  color: #222;
  &_white {
    color: #fff; } }

.subtitle {
  font-weight: 400;
  font-size: 17px;
  text-align: center;
  color: #222;
  &_white {
    color: #fff; } }

.foot_link {
  text-decoration: none;
  font-weight: 300;
  font-size: 11px;
  line-height: 618%;
  text-transform: uppercase;
  color: #999; }

.mobile {

  background: url("../image/bg_pc/mobile_bg.png") center center/cover no-repeat;
  padding: 46px 0 80px 0;
  .row {
    margin-top: 30px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    // opacity: 0.9
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 88.5px;
    &_1 {
      background: url('../image/cards/card_1.jpg') center center/cover no-repeat; }
    &_2 {
      background: url('../image/cards/card_2.jpg') center center/cover no-repeat; }
    &_3 {
      background: url('../image/cards/card_3.jpg') center center/cover no-repeat; }
    &_4 {
      background: url('../image/cards/card_4.jpg') center center/cover no-repeat; }
    &_5 {
      background: url('../image/cards/card_5.jpg') center center/cover no-repeat; }
    &_6 {
      background: url('../image/cards/card_6.jpg') center center/cover no-repeat; }
    &_subtitle {
      font-weight: 400;
      font-size: 24px;
      text-transform: uppercase;
      text-align: center;
      line-height: 31pxS;
      color: #fff; }
    &_plus {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      width: 27px;
      height: 26px;
      background-color: #000;
      margin: 0 auto;
      border-radius: 100%;
      padding: 6px;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      cursor: pointer;
      &::before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: $blue;
        transition: 0.3s all; }
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        height: 14px;
        width: 2px;
        background-color: $blue;
        transition: 0.3s all; }
      &:hover {
        &:before, &:after {
          background-color: $pink; } } } } }

.choise {
  position: relative;

  padding: 57px 0 137.5px 0;
  background: url('../image/white_bg.png') center center/cover no-repeat;

  &_car {
    position: relative;
    display: block;
    margin: 54px auto 0 auto;
    z-index: 2; }

  .choice_descr {
    font-weight: 300;
    font-size: 17px;
    line-height: 141%;
    text-align: center;
    color: #222; }

  .black {
    position: absolute;
    top: 0;
    left: 0;
    height: 499px;
    width: 100%;
    background-color: #070716;
    z-index: 1; }
  .label_white {
    position: relative;
    z-index: 2; }
  .title_white {
    position: relative;
    z-index: 2; }
  .subtitle_white {
    position: relative;
    z-index: 2; } }

.require {
  padding: 68px 0 87px 0;
  &_block {
    margin: 33px 0;
    &_nmb {
      margin-bottom: 0px; } }
  &_title {
    font-weight: 400;
    font-size: 22px;
    color: #1ca8c3; }

  &_list {
    list-style-type: none;
    padding-left: 50px;
    margin-top: 22px;
    li {
      position: relative;
      font-weight: 300;
      font-size: 17px;
      // line-height: 141%
      color: #222;
      margin-bottom: 10px;
      &::before {
        content: '';
        position: absolute;
        left: -32px;
        top: 3px;
        display: block;
        width: 21px;
        height: 20px;
        background: url('../icon/check_blue.svg') center center/cover no-repeat; } } }
  &_descr {
    font-weight: 300;
    font-size: 17px;
    line-height: 141%;
    color: #1ca8c3; }
  .warning {
    .require_title {
      color: $pink; }
    .require_list {
      li {
        &::before {
          background: url('../icon/check_red.svg') center center/cover no-repeat; } } } } }

.world {
  position: relative;
  padding: 48px 0 21.5px 0;
  min-height: 716px;

  background: url('../image/bg_pc/world_bg.png') center center/cover no-repeat;
  &_link {
    position: absolute;
    display: block;
    left: 50%;
    transform: translateX(-50%);
    bottom: 39px;
    text-decoration: none;
    font-weight: 400;
    font-size: 15px;
    line-height: 453%;
    text-transform: uppercase;
    text-align: center;
    color: #1fbad6;
    &:hover {
      color: #0000CD;
      transition: 0.3s all; } } }

footer {
  padding: 11px 21px 10.5px 23px; }

.footer {
  min-height: 133.5px;

  &_divider {
    display: block;
    margin: 0 auto;
    width: 301px;
    height: 7px;
    background-color: #1FBAD6; }
  &_wrapper {
    display: flex;
    margin: 48px 0 72px 23px; }
  &_item {
    display: flex;
    margin-right: 6px;
    &_round {
      width: 35px;
      height: 35px;
      border-radius: 100%;
      background-color: #000; } }
  .footer_links {
    width: 390px;
    margin: 0 auto;
    &_main {
      display: flex;
      justify-content: space-between;
      a {

        font-weight: 300;
        font-size: 15px;
        line-height: 453%;
        text-transform: uppercase;
        color: #070716; } } } }
